

@import "../modules/all";


* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

html, body {
  margin: 0;
  height: 100%;
  font-family: $default-font;
  color: $default-text-color;
}

.color--red {
  color: red;
  font-weight: bold;
}